body {
  background: rgb(157,38,5);
  background: linear-gradient(90deg, rgba(157,38,5,1) 0%, rgba(134,35,8,1) 35%, rgba(150,84,7,0.9023984593837535) 100%);
}

.App {
  background-color: #252525;
  min-height: 600px;
  width: 600px;
  margin: 40px auto;
  border-radius: 10px;
}
#to-do-form input{
  background-color: #ffdf91;
  border: 0;
  width: 380px;
  height: 60px;
  padding: 0 30px;
  margin: 20px;
  border-radius: 15px;
  color:#493323;
  font-size: 28px;
  font-family:'Verdana, sans-serif';
}

#to-do-form input::placeholder {
  color: #85603f;
}
#to-do-form input:focus, #to-do-form button:focus {
  outline: none;
}
#to-do-form button {
  height: 60px;
  width: 95px;
  border-radius: 5px;
  border: 0;
  background-color: #ffab73;
  cursor: pointer;
  font-weight: bolder;
  color: #493323;
  font-size: x-large;
}


 

